<template>
  <div id="history">
    <head-top title="历史课程"></head-top>
    <div class="main">
      <!-- 课程信息 -->
      <div class="item">
        <div class="tt">课程信息</div>
        <div class="content">
          <div class="name">课程名称：{{ courseInfo["CHName"] }}</div>
          <div class="name">科目：{{ courseInfo["CourseName"] }}</div>
          <div class="info">
            <span class="tc">老师：{{ courseInfo["TeacherName"] }}</span>
            <span class="gd">班级：{{ courseInfo["ClassName"] }}</span>
          </div>
          <div class="info">
            <span class="cs">教室：{{ courseInfo["ClassroomName"] }}</span>
            <span class="itime">
              时间：
              {{ courseInfo["CreateTime"] | formatDate(courseInfo["CreateTime"]) }}
            </span>
          </div>
        </div>
      </div>

      <!-- 签到信息 -->
      <div class="item">
        <div class="tt">签到记录</div>
        <div class="content">
          <div v-if="signList && signList.length>0"
            style="display: flex;justify-content: space-between;flex-wrap: wrap;">
            <van-tag v-if=" signList.length===1" plain type="success" style="margin-bottom:2vw;">
              #签到时间：{{signList[0].Key["SignTime"] | formatDate(signList[0].Key["SignTime"]) }}
            </van-tag>
            <van-tag v-else plain type="success" v-for="(item, index) in signList" :key="index"
              style="margin-bottom:2vw;">
              #第{{item['Number']}}次签到：
              {{item.Key["SignTime"] | formatDate(item.Key["SignTime"]) }}
            </van-tag>
          </div>
          <div v-else>
            无签到数据
          </div>
        </div>
      </div>

      <!-- 课程得分 -->
      <div class="item">
        <div class="tt">课程得分</div>
        <div class="content">
          <div class="zt">
            总分：
            <span>{{ courseScore["TotalScore"] }} 分</span>
          </div>
          <div class="sub">
            <span>
              截屏提问：
              <em>{{ courseScore["SAScore"] }}</em>
            </span>
            <span>
              小组讨论：
              <em>{{ courseScore["GDScore"] }}</em>
            </span>
            <span>
              随堂练习：
              <em>{{ courseScore["PAScore"] }}</em>
            </span>
          </div>
        </div>
      </div>

      <!-- 我的提问 -->
      <div class="item">
        <div class="tt">
          我的提问
          <span v-if="questionList.length > 2" @click="$router.push('/history/question')">更多</span>
        </div>
        <div class="content">
          <div class="questions" v-for="(item, i) in questionList" v-show="i < 2" :key="i">
            <div class="time">
              {{ item["CreateTime"] | formatDate(item["CreateTime"]) }}
            </div>
            <div class="info">{{ item["QContent"] }}</div>
          </div>
        </div>
      </div>

      <!-- 截屏提问 -->
      <div class="item">
        <div class="tt">截屏提问</div>
        <div class="content" v-if="screenQustionList.length > 0">
          <div class="sub" v-for="(item, index) in screenQustionList" :key="index"
            @click="selectScreenQuestion(item)">
            <span>{{ index + 1 + "、" + item.name }}</span>
            <span>
              题数：
              <em>{{ item.total }}</em>
              <i class="iconfont icon-qianjin"></i>
            </span>
          </div>
        </div>
        <div class="content" v-else>
          <div class="zt">暂无数据</div>
        </div>
      </div>

      <!-- 随堂练习 -->
      <div class="item">
        <div class="tt">随堂练习</div>
        <div class="content">
          <div class="sub" v-for="(item, index) in classPractiseList" :key="index"
            @click="selectClassPractise(item)">
            <span>{{ item["PLTitle"] }}</span>
            <span>
              题数：
              <em>{{ item["Total"] }}</em>
              <i class="iconfont icon-qianjin"></i>
            </span>
          </div>
        </div>
      </div>

      <!-- 小组讨论 -->
      <div class="item">
        <div class="tt">小组讨论</div>
        <div class="content">
          <div class="sub" v-for="(item, index) in groupDiscussionList" :key="index"
            @click="selectGroupDiscussion(item)">
            <span>{{ item["GroupName"] }}</span>
            <span>
              下发时间：
              <em>{{ item.Key["CreateTime"] | formatMinutesTime(item.Key["CreateTime"]) }}</em>

              <em>第{{ item["GroupingNumber"] || 0 }}次分组</em>

              <i class="iconfont icon-qianjin"></i>
            </span>
          </div>
        </div>
      </div>

      <!-- 课件 -->
      <div class="item">
        <div class="tt">
          课件
          <span v-if="courseWareList.length > 2" @click="$router.push('/courseware')">更多</span>
        </div>
        <div class="list">
          <van-cell v-for="(item, index) in courseWareList" :key="index" v-show="index < 2"
            arrow-direction="down" is-link @click="showPopup(index)">
            <template slot="title">
              <div class="list_cell">
                <div>
                  <i class="iconfont icon-word"></i>
                  <span class="title time">
                    {{item["MaterialName"] + "." + item["MaterialFileExt"]}}
                  </span>
                </div>
                <div class="size time">
                  <span v-if="item['MaterialFileSize'] > 1024 * 1024">
                    {{Math.round(item["MaterialFileSize"] / 1024.0 / 1024)}} MB
                  </span>
                  <span v-else>
                    {{ Math.round(item["MaterialFileSize"] / 1024) }} KB
                  </span>
                </div>
              </div>
            </template>
          </van-cell>
        </div>
      </div>

      <!-- 板书 -->
      <div class="item">
        <div class="tt">
          板书
          <span v-if="boardNoteList.length > 0"
            @click="$router.push('/history/boardnote')">更多</span>
        </div>
        <div class="content">
          <div class="sub-img">
            <div class="img" v-for="(item, index) in boardNoteList" v-show="index < 2" :key="index">
              <img :src="item.img" @click="preview(item.img)" alt="" />
            </div>
          </div>
        </div>
      </div>

      <!-- 小组笔记 -->
      <div class="item">
        <div class="tt">
          小组笔记
          <span v-if="groupNoteList.length > 0"
            @click="$router.push('/history/groupNote')">更多</span>
        </div>
        <div class="content">
          <div class="sub-img">
            <div class="img" v-for="(item, index) in groupNoteList" v-show="index < 2" :key="index">
              <img :src="item.img" @click="preview(item.img)" alt="" />
            </div>
          </div>
        </div>
      </div>

      <!-- 笔记 -->
      <div class="item">
        <div class="tt">
          笔记
          <span v-if="noteList.length > 0" @click="$router.push('/history/note')">更多</span>
        </div>
        <div class="content">
          <div class="sub" v-for="(item, index) in noteList" v-show="index < 3" :key="index"
            @click="selectNote(item)">
            <span>{{
              index +
                1 +
                "、" +
                (item["SNTtile"].length > 16 ? item["SNTtile"].substring(0, 16) + "..." : item["SNTtile"])
            }}</span>
            <span>
              <em>{{ item["CreateTime"] | formatMinutesTime(item["CreateTime"]) }}</em>
              <i class="iconfont icon-qianjin"></i>
            </span>
          </div>
        </div>
      </div>

      <!-- 课堂实况 -->
      <div class="item">
        <div class="tt">
          课堂实况
          <span v-if="miniCourseList.length > 1"
            @click="$router.push('/history/liveLesson')">更多</span>
        </div>
        <div class="content">
          <div v-for="(item, index) in miniCourseList" v-show="index < 1" :key="index"
            class="list_item">
            <t-video :src="item.src" :poster="item.poster"></t-video>
            <div class="line_mark">
              <span v-if="item.title">{{ item.title }}</span>
              <span v-else>{{ item.time | formatDate(item.time) }}</span>
              <span>{{ item.typeName }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <van-popup v-model="show" position="bottom">
      <van-cell value="预览" @click.stop.prevent="previewFile">
        <van-icon slot="right-icon" class-prefix="iconfont icon-yulan"></van-icon>
      </van-cell>
      <van-cell value="下载" @click.stop.prevent="download">
        <van-icon slot="right-icon" class-prefix="iconfont icon-xiazai"></van-icon>
      </van-cell>
    </van-popup>
  </div>
</template>

<script>
import { ImagePreview } from "vant";
import tVideo from "@/components/r-video";
import defaultBg from "@/assets/video/videopost.png";
import { ModulePicture } from "@/utils/enum";
import { screenQuestionTypeName, microLessonTypeName } from "@/utils/enum";
export default {
  name: "",
  components: { tVideo },
  props: {},
  data() {
    return {
      courseID: this.$store.getters.courseID,
      studentID: this.$store.getters.studentID,
      courseInfo: this.$store.getters.course,
      imagePreview: null,

      signList: [],
      courseScore: {},
      questionList: [],
      screenQustionList: [],
      classPractiseList: [],
      groupDiscussionList: [],
      courseWareList: [],
      boardNoteList: [],
      groupNoteList: [],
      miniCourseList: [],
      noteList: [],

      show: false,
      selectIndex: -1
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.getSign();
      this.getCourseScore();
      this.getQuestion();
      this.getScreenQuestion();
      this.getClassPractise();
      this.getGroupDiscussion();
      this.getCourseWare();
      this.getBoardNote();
      this.getGroupNote();
      this.getMiniCourse();
      this.getNote();
    });
  },
  methods: {
    getSign() {
      let reqData = { ciID: this.courseID, studentID: this.studentID };
      this.signList = [];
      this.$mainHttp.sign
        .getList(reqData)
        .then(res => {
          if (res.Result) {
            res.Datas.forEach(item => {
              this.signList.push(item);
            });
          }
        })
        .catch(err => {});
    },
    getCourseScore() {
      this.courseScore = {};
      let reqData = { ciID: this.courseID, studentID: this.studentID };
      this.$mainHttp.classInfo
        .getStudentScore(reqData)
        .then(res => {
          if (res.Result && res.Data) {
            this.courseScore = res.Data;
          }
        })
        .catch(err => {});
    },
    getQuestion() {
      this.questionList = [];
      let reqData = { CIID: this.courseID, studentID: this.studentID };
      this.$mainHttp.question
        .getList(reqData)
        .then(res => {
          if (res.Result) {
            if (res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.questionList.push(item);
              });
            }
          }
        })
        .catch(err => {});
    },
    getScreenQuestion() {
      this.screenQustionList = [];
      let reqData = { ciID: this.courseID };
      this.$mainHttp.screenQustion
        .getListByType(reqData)
        .then(res => {
          if (res.Result) {
            if (res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.screenQustionList.push({
                  type: item["Type"],
                  name: screenQuestionTypeName(item["Type"]),
                  total: item["Total"]
                });
              });
            }
          }
        })
        .catch(err => {});
    },
    getClassPractise() {
      this.classPractiseList = [];
      let reqData = { ciID: this.courseID };
      this.$mainHttp.classPractise
        .getStatistic(reqData)
        .then(res => {
          if (res.Result) {
            if (res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.classPractiseList.push(item);
              });
            }
          }
        })
        .catch(err => {});
    },
    getGroupDiscussion() {
      this.groupDiscussionList = [];
      let reqData = { ciID: this.courseID, studentID: this.studentID };
      this.$mainHttp.groupDiscussion
        .getList(reqData)
        .then(res => {
          if (res.Result) {
            if (res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.groupDiscussionList.push(item);
              });
            }
          }
        })
        .catch(err => {});
    },
    getCourseWare() {
      this.courseWareList = [];
      let reqData = { ciID: this.courseID };
      this.$mainHttp.courseware
        .getList(reqData)
        .then(res => {
          if (res.Result) {
            if (res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.courseWareList.push(item);
              });
            }
          }
        })
        .catch(err => {});
    },
    getBoardNote() {
      this.boardNoteList = [];
      let reqData = { ciID: this.courseID };
      this.$mainHttp.boardNote
        .getList(reqData)
        .then(res => {
          if (res.Result) {
            if (res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.boardNoteList.push({
                  img: this.$mainHttp.resourse.picture(
                    item.BNContent,
                    ModulePicture.BoardNote
                  ),
                  state: item.State,
                  title: item.BNTitle,
                  time: item.CreateTime,
                  id: item.BNID,
                  content: item.BNContent
                });
              });
            }
          }
        })
        .catch(err => {});
    },
    getGroupNote() {
      this.boardNoteList = [];
      let reqData = { ciID: this.courseID, studentID: this.studentID };
      this.$mainHttp.groupNote
        .getList(reqData)
        .then(res => {
          if (res.Result) {
            if (res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.groupNoteList.push({
                  img: this.$mainHttp.resourse.picture(
                    item.GNContent,
                    ModulePicture.groupNote
                  ),
                  state: item.State,
                  title: item.GNTitle,
                  time: item.CreateTime,
                  id: item.GNID,
                  content: item.GNContent
                });
              });
            }
          }
        })
        .catch(err => {});
    },
    getMiniCourse() {
      this.miniCourseList = [];
      let reqData = { ciID: this.courseID };
      this.$mainHttp.miniCourse
        .getList(reqData)
        .then(res => {
          if (res.Result) {
            if (res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.miniCourseList.push({
                  title: item["MLTitle"],
                  time: item["StartTime"],
                  typeName: microLessonTypeName(item["MLType"]),
                  src: this.$mainHttp.resourse.video(
                    item["MLID"],
                    item["FileFormat"]
                  ),
                  poster: defaultBg
                });
              });
            }
          }
        })
        .catch(err => {});
    },
    getNote() {
      this.noteList = [];
      let reqData = { ciID: this.courseID, studentID: this.studentID };
      this.$mainHttp.note
        .getList(reqData)
        .then(res => {
          if (res.Result) {
            if (res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.noteList.push(item);
              });
            }
          }
        })
        .catch(err => {});
    },

    getsignStateName(value) {
      if (value === 1) {
        return "正常";
      } else if (value === 2) {
        return "迟到";
      } else {
        return "";
      }
    },
    selectScreenQuestion(item) {
      if (item.total !== 0)
        this.$router.push("/history/screenQuestion/" + item.type);
    },
    selectClassPractise(item) {
      this.$router.push(
        "/history/classPractise/" +
          item["CIPID"] +
          "/" +
          item.Key["PLID"] +
          "/" +
          item["Total"]
      );
    },
    selectGroupDiscussion(item) {
      this.$router.push(
        "/history/groupDiscussion/" +
          item.Key["GSID"] +
          "/" +
          item.Key["GroupID"] +
          "/" +
          item["GroupName"]
      );
    },
    selectCourseWare(item) {
      // let fileUrl = this.$mainHttp.resourse.file(item.Value["MaterialID"]);
      // if (!window.webkit) {
      //   window.location.href = fileUrl;
      //   this.$toast("下载开始");
      // } else {
      //   sendToAPP(FuncType.downloadFile, fileUrl)
      //     .then(res => {})
      //     .catch(err => {});
      // }
    },
    showPopup(index) {
      this.selectIndex = index;
      this.show = true;
    },
    download() {
      let item = this.courseWareList[this.selectIndex];
      let fileUrl = this.$mainHttp.resourse.file(item["MaterialID"]);
      if (!window.webkit) {
        window.location.href = fileUrl;
        this.show = false;
        this.$toast("下载开始");
      } else {
        this.show = false;
        sendToAPP(FuncType.downloadFile, fileUrl)
          .then(res => {})
          .catch(err => {});
      }
    },
    previewFile() {
      let item = this.courseWareList[this.selectIndex];
      this.$router.push(
        `/history/coursewareView/${item["MaterialID"]}/${
          item["MaterialFileExt"]
        }/${item["MaterialName"]}`
      );
    },
    // selectBoardNote(item) {},
    selectNote(item) {
      this.$router.push({
        path: "/history/note/detail",
        query: {
          title: item["SNTtile"],
          type: item["SNForm"],
          content: item["SNContent"],
          createTime: item["CreateTime"],
        }
      });
    },

    preview(img) {
      this.imagePreview = ImagePreview({
        images: [img],
        showIndex: false,
        onClose() {}
      });
    }
  },
  computed: {},
  watch: {},
  destroyed() {
    if (this.imagePreview) this.imagePreview.close();
  }
};
</script>

<style lang="less">
#history {
  width: 100%;
  height: 100%;
  .main {
    background: #ebf0ef;
  }
  .item {
    text-align: left;
    background: #fff;
    padding: 4vw;
    margin-bottom: 15px;

    .tt {
      font-size: 16px;
      color: rgba(51, 51, 51, 1);
      border-left: 3px solid #00cbab;
      padding-left: 10px;
      box-sizing: border-box;
      margin-bottom: 18px;
      span {
        float: right;
        color: #666666;
        font-size: 1.2rem;
      }
    }
    .content {
      padding-left: 13px;
      color: #666666;
      font-size: 1.2rem;
      line-height: 1.5;
      .name,
      .info {
        margin-bottom: 10px;
      }
      .zt,
      .sj {
        span {
          color: black;
        }
      }
      .info {
        display: flex;
        justify-content: space-between;
        .tc,
        .cs {
          // width: 45%;
        }
        .gd,
        .itime {
          width: 60%;
        }
      }
      // .icon-yulan {
      //   padding-left: 1vw;
      //   color: #07c160;
      // }
      // .icon-xiazai {
      //   padding-left: 1vw;
      //   color: #ff976a;
      // }

      .sub-img {
        display: flex;
        justify-content: space-between;
        margin-bottom: 5px;
        margin-right: 7px;
        .img {
          width: 40.3vw;
          height: 29.6vw;
          display: flex;
          align-items: center;
          justify-content: center;
          img {
            max-width: 40.3vw;
            max-height: 29.6vw;
          }
        }
        .yd-img {
          margin-right: 7px;
          background-image: url("../../assets/bgPaly1.jpg");
          background-size: cover;

          width: 85vw;
          height: 55vw;
        }
      }
      .sub {
        width: 85vw;
        height: 11.5vw;
        line-height: 11.5vw;
        border-bottom: 1px solid #dddddd;
        color: #666666;
        display: flex;
        justify-content: space-between;
        font-size: 1.2rem;
        em {
          color: #00cbab;
          margin-right: 20px;
        }
        .icon-qianjin {
          font-size: 1rem !important;
          color: #999999;
        }
        .sub_left {
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          word-break: break-all;
        }
      }

      .list_item {
        margin-top: 2vh;
        .video-player {
          width: 100%;
          min-height: 30vh;
          margin: 0 auto;
          position: relative;
          overflow: hidden;
          border-radius: 1rem;

          video {
            width: 100%;
            margin: 0 auto;
          }
        }
        .line_mark {
          position: relative;
          display: flex;
          justify-content: space-between;
          margin: 0 3vw;
          font-size: 1.25rem;
          line-height: 3;
          color: #777;
        }
      }

      .questions {
        width: 100%;
        padding-top: 0.5rem;
        text-align: left;
        background: rgba(255, 255, 255, 1);
        border-bottom: 1px solid #dddddd;
        border-radius: 3px;

        word-break: break-all;
        word-wrap: break-word;
        .time {
          font-size: 1.2rem;
          color: #00cbab;
          line-height: 1.5;
        }
        .info {
          font-size: 1.25rem;
          line-height: 1.6;
          color: rgba(51, 51, 51, 1);
        }
      }

      .video-player {
        width: 100vw;
        min-height: 55vw;
        margin: 0 auto;
        position: relative;
        overflow: hidden;

        video {
          width: 100%;
          margin: 0 auto;
        }
      }
    }

    .list {
      margin: -4vw;
      .list_cell {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-right: 2vw;

        .icon-word {
          color: #409eff;
        }
        .size {
          text-align: right;
        }
      }
    }
  }

  .icon-yulan {
    padding-left: 1vw;
    color: #07c160;
  }
  .icon-xiazai {
    padding-left: 1vw;
    color: #ff976a;
  }
}
</style>



